.page--work {
    padding-top: 8.5rem;
    padding-bottom: 8.5rem;
    background: rgb(200,200,200);
    overflow-x: hidden;
}

.page--work .pane {
    position: relative;
    padding: 4.5rem;
    height: 798px;
}

.page--work h2.work--title {
    font-family: 'WhyteTrail-Light', 'Work Sans', sans-serif;
    font-size: 8.5vw;
    padding: 4.5rem 0;
    letter-spacing: -0.12rem;
    color: rgb(63,63,63);
    position: absolute;
    z-index: 1;
    top: 200px;
}

.page--work h2.work--title > a {
    text-decoration: none;
    color: rgb(255,255,255);
}

.page--work .work--pane {
    height: 475px;
    width: 720px;
    background-color: rgb(100,100,100);
    position: absolute;
    right: 100px;
    top: 150px;
    opacity: 0;
    transform: translateY(0px);
    transition: all .3s ease-in-out;
}

.w--pane--letts {
    background: url('../assets/img/work_pane_letts.jpg') center center no-repeat;
    background-size: cover;
    opacity: 0;
    width: 100%;
    height: 100%;
}

.w--pane--moody {
    background: url('../assets/img/work_pane_moody.jpg') center center no-repeat;
    background-size: cover;
    opacity: 0;
    width: 100%;
    height: 100%;    
}

.w--pane--atar {
    background: url('../assets/img/work_pane_atar.jpg') center center no-repeat;
    background-size: cover;
    opacity: 0;
    width: 100%;
    height: 100%;    
}

/* Work Backgrounds */

body.work--letts .background__wrapper,
body.work--letts .page__part__one {
   background-color: rgb(101,105,199);
   background-image: none;
}
body.work--moody .background__wrapper {
    background-image: url('../assets/img/trevor-brown-ifgisB1W8w8-unsplash.jpg');
}
body.work--deakin .background__wrapper {
    background-image: url('../assets/img/nastuh-abootalebi-yWwob8kwOCk-unsplash.jpg');
}

body.work--letts .page__part__one .fixed {
    background-image: url('../assets/img/letts-fixed-asset.png');
    background-color: transparent;
    position: fixed;
    bottom: 0;
    left: 50%;
    right: 50%;
    height: 50vh;
    width: 100vw;
}